.homePage-items-container {
  padding: 0 20px 20px 20px;
}
.homePage-items-container .card .cardTitle {
  font-size: 16px;
  color: #19233C;
  font-weight: bold;
}
.homePage-items-container .card .filterBox {
  margin-top: 20px;
  margin-bottom: 20px;
}
.homePage-items-container .card .filterBox .filterItem {
  float: left;
  margin-right: 16px;
  margin-bottom: 8px;
}
.homePage-items-container .card .filterBox:after {
  content: "";
  display: table;
  clear: both;
}
.homePage-items-container .card .link {
  color: #24695C;
  cursor: pointer;
}
.selectRow {
  background: #e6f7ff;
}
