.homepage-new-container .left-container .modules-container .module-page:after {
  content: "";
  display: table;
  clear: both;
}
.homepage-new-container .left-container .modules-container .toggle-button {
  text-align: center;
  margin-top: 15px;
}
.homepage-new-container .left-container .modules-container .toggle-button span {
  display: inline-block;
  width: 14px;
  height: 8px;
  border-radius: 7px;
  margin: 0 5px;
  background: #D8D2CF;
  cursor: pointer;
}
.homepage-new-container .left-container .modules-container .toggle-button span.active {
  background: #C08E60;
  width: 36px;
}
.homepage-new-container .left-container .items-container {
  flex: 1 1;
  overflow: auto;
  background: #fff;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.05);
  border-radius: 4px;
  padding: 20px 30px;
}
.homepage-new-container .left-container .items-container .ant-tabs-extra-content .filter-item {
  margin-left: 10px;
  float: left;
}
.homepage-new-container .left-container .copyr-right {
  text-align: center;
  height: 54px;
  line-height: 54px;
  margin-bottom: 0px;
}
.homepage-new-container .right-container {
  flex: 0 0 300px;
  display: flex;
  overflow: hidden;
  flex-direction: column;
  background: #FAFAFA;
}
.homepage-new-container .right-container .info-card {
  height: 366px;
  border-radius: 8px;
  background: #24695C;
  text-align: center;
  position: relative;
  color: #fff;
  padding-top: 43px;
  background-image: url(/static/media/info-bg.52390f1a.png);
}
.homepage-new-container .right-container .info-card .anticon {
  position: absolute;
  right: 22px;
  top: 22px;
  font-size: 20px;
}
.homepage-new-container .right-container .info-card img {
  width: 122px;
  height: 154px;
  padding: 8px;
  border: 1px dashed #FFFFFF;
  border-radius: 8px;
}
.homepage-new-container .right-container .item-card {
  height: 80px;
  margin: 20px;
  display: flex;
  justify-content: space-around;
}
.homepage-new-container .right-container .item-card .split-line {
  width: 1px;
  background: #848484;
}
.homepage-new-container .right-container .item-card .part {
  flex: 1 1;
  text-align: center;
  position: relative;
}
.homepage-new-container .right-container .item-card .part span {
  font-size: 36px;
}
.homepage-new-container .right-container .item-card .part p {
  margin: 5px 0 0 0;
  color: #020202;
}
.homepage-new-container .right-container .info-note {
  flex: 1 1;
  overflow: auto;
  padding: 20px 40px 0px 40px;
}
.homepage-new-container .right-container .info-note .title {
  font-size: 16px;
  font-weight: bold;
  color: #000;
}
.homepage-new-container .right-container .info-note .NewsList {
  padding: 0;
  padding-top: 20px;
  padding-left: 10px;
  list-style: none;
}
.homepage-new-container .right-container .info-note .NewsList li {
  margin-bottom: 10px;
  cursor: pointer;
}
.homepage-new-container .right-container .info-note .NewsList li:hover {
  background: #F6F7FF;
}
.homepage-new-container .right-container .info-note .NewsList li p {
  margin-bottom: 6px;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
  color: rgba(0, 0, 0, 0.45);
}
.homepage-new-container .right-container .info-note .NewsList li p.readStatus {
  color: #2A3141;
}
.homepage-new-container .right-container .info-note .NewsList li p.readStatus span {
  color: #C9565C;
}
.homepage-new-container .right-container .info-note .NewsList li p span {
  margin-right: 14px;
}
